import React from 'react';
import './MainPartUp.css';
import {
    withRouter
} from 'react-router-dom'
import { withFirebase } from "../Fire/index"

class RegistrationFormBase extends React.Component {

    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            emailIsValid: true,
            passwordIsValid: true,
            passwordsAreEqual: true,
            
        }
    }

    handleSubmit(e) {
        let emailIsValidB = true;
        let passwordIsValidB = true;
        let passwordsAreEqualB = true;
        

        if (!/^[\w.-]+@[\w.-]+.\w{2,4}$/.test(this.email.value)) {
            emailIsValidB = false;
        }
        if (!/\w/g.test(this.password1.value)) {
            passwordIsValidB = false;
        }
        if (this.password1.value !== this.password2.value) {
            passwordsAreEqualB = false;
        }
        
       
        this.setState({
            emailIsValid: emailIsValidB,
            passwordIsValid: passwordIsValidB,
            passwordsAreEqual: passwordsAreEqualB,
            
            
        });
        if (emailIsValidB & passwordIsValidB & passwordsAreEqualB ) {
            this.props.firebase
                .doCreateUserWithEmailAndPassword(this.email.value, this.password1.value)
                .then(authUser => {
                    this.props.firebase
                        .storageRef(authUser.user.uid);
                    this.props.history.push("/homework");
                })
                .catch(error => {
                    alert(error);
                });
        }
        e.preventDefault();
    }

    render() {
        return (
            <div className="Registration-form">
                <form onSubmit={this.handleSubmit}>
                    
                    <p className="Input-label">Почта</p>
                    <p className="Alert-label">
                        {this.state.emailIsValid ? "" : "Неверные символы!"}
                    </p>
                    <input
                        type="text"
                        className="Text-input"
                        ref={(input) => this.email = input}
                    />
                    <p className="Input-label">Введите пароль</p>
                    <p className="Alert-label">
                        {this.state.passwordIsValid ? "" : "Минимум 6 символов!"}
                    </p>
                    <input
                        type="password"
                        className="Text-input"
                        ref={(input) => this.password1 = input}
                    />
                    <p className="Input-label">Повторите пароль</p>
                    <p className="Alert-label">
                        {this.state.passwordsAreEqual ? "" : "Пароли не совпадают!"}
                    </p>
                    <input
                        type="password"
                        className="Text-input"
                        ref={(input) => this.password2 = input}
                    />
                 
                    <input
                        type="submit"
                        className="Button"
                        value="Готово"
                    />
                    <p className="Result-label"></p>
                    
                </form>
                
            </div>
        );
    }
}

const RegistrationForm = withFirebase(withRouter(RegistrationFormBase));

export default RegistrationForm