

import React from 'react';
import './previewIn.css';
import {  withRouter} from 'react-router-dom'
import { withFirebase } from "../Fire/index"

class SignInFire extends React.Component {

    constructor(props) {
        super(props);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            infoIsValid: true,
        }
    }

    handleSubmit(e) {
        this.props.firebase
            .doSignInWithEmailAndPassword(this.email.value, this.password.value)
            .then(() => {
                this.props.history.push("/homework");
            })
            .catch(error => {
                alert(error);
                this.setState({ infoIsValid: false });
            });

        e.preventDefault();
    }

    render() {
        return (
            <div className="Login-form">
                
                <form onSubmit={this.handleSubmit}>
                
                    <p className="Alert-label">
                        {this.state.infoIsValid ? "" : "Данные не найдены или введены некорректо!"}
                    </p>
                    <p className="Input-label">Введите почту</p>
                    <input
                        type="text"
                        className="Text-input"
                        ref={(input) => this.email = input}
                    />
                    <p className="Input-label">Введите пароль</p>
                    <input
                        type="password"
                        ref={(input) => this.password = input}
                        className="Text-input"
                    />
                    <input type="submit" className="Button" value="Войти" />
                    <p className="Result-label"></p>
                   
                </form>
               
            </div>
        );
    }
}

const LoginForm = withFirebase(withRouter(SignInFire));

export default LoginForm