
import React from 'react';
import './Header.css';
import Icon from '../../128.gif'
import { Link } from 'react-router-dom'
import { withFirebase } from '../../components/Fire/index'

class HeaderBase extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            authUser: null,
        };
    }

    componentDidMount() {
        this.listener = this.props.firebase.auth.onAuthStateChanged(authUser => {
            authUser
            ? this.setState({ authUser })
            : this.setState({ authUser: null })
        });
    }

    componentWillUnmount() {
        this.listener();
    }

    render() {
        
        return (
            <div className="Header">
                <div className="heading">
                <p className="Fall"><img src={Icon} alt="icon" /></p>
                    <p className="Title">Курс валют</p>
                </div>
                <div className="Auth">
                    {this.state.authUser ? <AuthButtons /> : <NonAuthButtons />}
                </div>
            </div>
        );
    }
}

const AuthButtons = withFirebase(({ firebase }) => (
    <>
        <div className="Upper-button">
            <Link className="Link" to="/homework/Converter">Конвентер</Link>
        </div>
        <div className="Lower-button" onClick={firebase.doSignOut}>
            Выйти из аккаунта
        </div>
    </>
));
const NonAuthButtons = () => (
    <>
        <div className="Upper-button">
            <Link className="Link" to="/homework/SignIn">Войти</Link>
        </div>
        <div className="Lower-button">
            <Link className="Link" to="/homework/SignUp">Зарегистрироваться</Link>
        </div>
    </>
);
const Header = withFirebase(HeaderBase);
export default Header


