import React from 'react';
import './MainPartConv.css';
//import Icon from '../icons/icons'

class MainPartConv extends React.Component {

    constructor() {
        super();
        this.state = {
            isLoaded: false,
            json: {},
        }
    }

    componentDidMount() {
        fetch("https://api.exchangeratesapi.io/latest?base=RUB")
            .then(response => response.json())
            .then(result => this.setState({
                isLoaded: true,
                json: result,
            }));
    }

    getRubPrice(name) {
        switch (name) {
            case "USD":
                return this.state.json.USD;
            case "EUR":
                return this.state.json.EUR;
            case "GBP":
                return this.state.json.GBP;
            case "SEK":
                return this.state.json.SEK;
            case "CHF":
                return this.state.json.CHF;
                case "NOK":
                return this.state.json.NOK;
            default:
                return null;
        }
    }

    convertMoney(value, name) {
        if (name !== "RUB") {
            this.Rub.value = (value / this.getRubPrice(name)).toFixed(2);
        }
        if (name !== "USD") {
            this.Usd.value = (this.Rub.value * this.state.json.USD).toFixed(2);
        }
        if (name !== "EUR") {
            this.Eur.value = (this.Rub.value * this.state.json.EUR).toFixed(2);
        }
        if (name !== "GBP") {
            this.Gbp.value = (this.Rub.value * this.state.json.GBP).toFixed(2);
        }
        if (name !== "SEK") {
            this.Sek.value = (this.Rub.value * this.state.json.SEK).toFixed(2);
        }
        if (name !== "CHF") {
            this.Chf.value = (this.Rub.value * this.state.json.CHF).toFixed(2);
        }
        if (name !== "NOK") {
            this.Nok.value = (this.Rub.value * this.state.json.NOK).toFixed(2);
        }

    }


    render() {
        return (
            <div className="Converter">
                <table className="TableConv">
                    <tbody>
                        <tr>
                            <td className="TD">
                                <p>РОССИЙСКИЙ РУБЛЬ</p>
                                <div className="Input-line">
                                    <p>RUB =</p>
                                    <input
                                        type="number"
                                        name="RUB"
                                        className="Input-box"
                                        ref={(input) => this.Rub = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                            <td className="TD">
                                <p>ДОЛЛАР США</p>
                                <div className="Input-line">
                                    <p>USD =</p>
                                    <input
                                        type="number"
                                        name="USD"
                                        className="Input-box"
                                        ref={(input) => this.Usd = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                        </tr>
                       
                            <td className="TD">
                                <p>ЕВРО</p>
                                <div className="Input-line">
                                    <p>EUR =</p>
                                    <input
                                        type="number"
                                        name="EUR"
                                        className="Input-box"
                                        ref={(input) => this.Eur = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                            <td className="TD">
                                <p>ФУНТ СТЕРЛИНГОВ</p>
                                <div className="Input-line">
                                    <p>GBP =</p>
                                    <input
                                        type="number"
                                        name="GBP"
                                        className="Input-box"
                                        ref={(input) => this.Gbp = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                        
                        <tr>
                            <td className="TD">
                                <p>Шведская Крона</p>
                                <div className="Input-line">
                                    <p>SEK =</p>
                                    <input
                                        type="number"
                                        name="SEK"
                                        className="Input-box"
                                        ref={(input) => this.Sek = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                            <td className="TD">
                                <p>Швейцарский франк</p>
                                <div className="Input-line">
                                    <p>CHF =</p>
                                    <input
                                        type="number"
                                        name="CHF"
                                        className="Input-box"
                                        ref={(input) => this.Chf = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                            <td className="TD">
                                <p>Норвежская крона</p>
                                <div className="Input-line">
                                    <p>NOK =</p>
                                    <input
                                        type="number"
                                        name="NOK"
                                        className="Input-box"
                                        ref={(input) => this.Nok = input}
                                        onInput={event => this.convertMoney(event.target.value, event.target.name)}
                                    />
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        );
    }

}
export default MainPartConv